import React from 'react'
import ComMenu from '@/components/comMenu/ComMenu.jsx'

import { useTranslation } from "react-i18next";
export default function UserCenter (props) {
  const { t } = useTranslation();
  const menus = [
    {
      title: t('account.profile'),
      to: 'profile'
    },
    {
      title: t('account.ssh'),
      to: 'ssh'
    },
    {
      title: 'API',
      to: 'api'
    },
    {
      title: t('account.operation_log'),
      to: 'log'
    },
  ]

  return (
    <>
      <ComMenu menus={menus} ></ComMenu >
    </>
  )
}
